@media #{$mobile} {
    .website-award {
        margin-top: 14px;
        margin-left: 35px;
        max-width: 200px;
    }
}

@media (max-width:400px) {
    .website-award {
        margin-top: 22px;
        margin-left: 18px;
        width:140px;
    }
