    .voyager-logo {
        display:none;
    }
}


.bannerad{
    @media screen and (max-width: $sm-break-down) {
        padding-top: 140px;
        
    }
}

.site-header {
    background:white;

    padding-top: 18px;
    @media #{$tablet} {
        padding-top: 0;
    }
    @media screen and (max-width: $sm-break-down) {
        padding-top: 0;
        top: 0;
        position: absolute;
        width: 100%;
    }
    // @media screen and (max-width: $mobile-break){
    //     position: fixed;
    //     top:0;
    //     width: 100%;
    //     background: #FFF;
    //     z-index: 100;
    // }
    .site-branding-top{
        @media #{$tablet} {
            display: none;
        }

        .social-links{
            a {
                color: $gray-base;
            }
            .logedin {
                margin-left:20px;
            }
        }

        .secondary-menu{
            > ul {
                @extend %ulmenu;
                font-size: em(14);
                font-weight: 600;

                li{
                    margin-right: 20px;
                    a {
                        color:#8a8b93;
                    }
                }
            }
        }

        .section-right{
            margin-right: 0;
            @media screen and (min-width: $header-break){
                // width: 895px;
            }
        }

        @media screen and (max-width: $sm-break-down) {
            display: none;
        }
    
        .rooms-link{
            float: right;
            margin-left: -30px;
            
            @media screen and (max-width: $header-break){
                display: none;
            }

            > ul{
                @extend %ulmenu;

                li{
                    a{
                        display: block;
                        margin-left: 30px;

                        img{
                            height: 20px;
                            width: auto;
                            max-width: 100%;
                        }
                        // &.newsroom{
                        //     img{
                        //         height: 14px;
                        //         margin-top: 3px;
                        //     }
                        // }
                    }
                }
            }
            
        }

        &.foldaway {
            border-bottom:1px solid #6c6c6c;
            border-top:1px solid #6c6c6c;
            margin-top:50px;


            .rooms-link{
                float: none;
                // padding-top: 0;
                margin-left: 0;
            }


            .press-patron {
                display:none;
            }

            .section-left {
                padding-top:10px;
            }

            .section-right {
                width:auto;
                margin-right: 65px;
            }


            .social-links{
                display:inline-block;
                
            }
            
            .secondary-menu {
                display:inline-block;

                > ul > li > a {
                    color:#6c6c6c;
                    font-weight:normal;

                }

            }
        }

    }






    .site-branding-bottom{
        padding-top: 6px;
        // padding-bottom: 40px;
        @media #{$tablet} {
            position: absolute;
            top: 0;
            width: 100%;
            padding-bottom: 15px;
            border-bottom: 1px solid #8a8b93;

        }

        @media screen and (max-width: $sm-break-down) {
            padding-top: 10px;
            padding-bottom: 10px;
        }

        .section-right{
            @media #{$desktop-up} {
                margin-top: -15px;
            }
            @media screen and (min-width: $header-break){
                padding-left: 0;
                // padding-right: 0;
                float: right;
                margin-left: 0;
                // margin-right: 15px;
            }
            > .sb-custom-menu{
                border-top: solid 4px #000000;

                @media screen and (max-width: $header-break){
                    border: 0;
                }
            }
        }



        .navbar-brand{
            padding: 0;
            height: auto;
            @media screen and (max-width: $sm-break-down) {
                display: flex;
                flex-direction: row;
            }

            .main-logo{
                max-width:400px;
                height: 61px;

                @media screen and (max-width: $sm-break-down) {
                    display: none;
                }
            }

            .mobile-logo{
                display: none;
                @media screen and (max-width: $sm-break-down) {
                    display: flex;
                    width: 63px;
                    height:45px;
                }
            }
            .mobile-logo-summer {
                display: none;
                @media screen and (max-width: $sm-break-down) {
                    display: flex;
                    width: 62px;
                    margin-top: 13px;
                }
            }
        }
        &.foldaway {
            color:#6c6c6c;
            padding:0;
            padding-top:10px;
            // border:1px solid green;

            .sb-custom-menu{

                @media (max-width: 1370px){
                    
                    > ul{
                        display:block;
                        
                        > li {
                            width: auto;
                            float: none;
                            display: inline-block;
                            border: none;
                        }

                    }
                }


                border-top: none;
                text-align: right;

                a {
                    text-transform:none;
                    color:#6c6c6c;
                    font-weight: normal;
                }
            }
        }
    }


        


    .site-branding-bottom-foldaway {
        @include transition(100000s);
        background:white;
        position:fixed;
        top:-60px;
        right:0;
        padding:10px 0 5px 0;
        margin:0;
        width:100%;
        border-bottom: solid 1px #6c6c6c;
        z-index:2147483647;
        
        .navbar-brand{
            padding:0;
            height:auto;

            .mobile-logo{
                display: block;
                width: 63px;
            }
        }

        .sb-custom-menu-foldaway {
            float:right;
            margin-right: 50px;
            .menu-mobile{
                display: block;
                width: 95px;
                background-image: url(../images/mobile-menu.svg);
                background-size: 45px 30px;
                background-position: right;
                background-repeat: no-repeat;
                padding: 5px 0;
                color: #f73d30;
            }
        }
        .site-branding-bottom {
            margin-right: 50px;
        }

    }

}